"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { Badge } from "@/components/ui/badge";

interface Order {
    id: number;
    reference: string;
    recipient_phone: string;
    cost: number;
    status: string;
    created_at: string;
    bundle?: {
        name: string;
        network: string;
    };
}

export default function OrdersPage() {
    const [orders, setOrders] = useState<Order[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        api.get('/orders').then(res => {
            setOrders(res.data.data);
            setLoading(false);
        });
    }, []);

    return (
        <div className="space-y-6">
            <div className="flex justify-between items-center">
                <h2 className="text-3xl font-bold tracking-tight">Your Orders</h2>
                <Button asChild>
                    <Link href="/dashboard/orders/new">New Order</Link>
                </Button>
            </div>

            <div className="bg-white rounded-md border">
                <table className="w-full text-sm text-left">
                    <thead className="bg-gray-50 uppercase text-gray-500">
                        <tr>
                            <th className="px-6 py-3">Reference</th>
                            <th className="px-6 py-3">Bundle</th>
                            <th className="px-6 py-3">Recipient</th>
                            <th className="px-6 py-3">Cost</th>
                            <th className="px-6 py-3">Status</th>
                            <th className="px-6 py-3">Date</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200">
                        {loading ? (
                            <tr><td colSpan={6} className="p-4 text-center">Loading...</td></tr>
                        ) : orders.map((order) => (
                            <tr key={order.id} className="hover:bg-gray-50 transition-colors">
                                <td className="px-6 py-4 font-mono">
                                    <Link href={`/dashboard/orders/${order.id}`} className="text-blue-600 hover:underline">
                                        {order.reference}
                                    </Link>
                                </td>
                                <td className="px-6 py-4">{order.bundle?.network} - {order.bundle?.name}</td>
                                <td className="px-6 py-4">{order.recipient_phone}</td>
                                <td className="px-6 py-4 font-bold">GHS {order.cost}</td>
                                <td className="px-6 py-4">
                                    <Badge variant={order.status === 'completed' ? 'default' : 'secondary'}>
                                        {order.status}
                                    </Badge>
                                </td>
                                <td className="px-6 py-4">{new Date(order.created_at).toLocaleDateString()}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
